import React = require("react");
import Video from "../components/video";
import Sequence from "../components/sequence";
import { ipmiConfig } from "../ipmi_config";
import { observable } from "mobx";
import { observer } from "mobx-react";

const IPMI = require("../lib/ipmi.js");
IPMI.Tools.DEBUG = true;
const IPMIFramework = new IPMI.Framework(ipmiConfig);

@observer
export default class DemoApp1 extends React.Component {
  @observable private video1: Video;
  @observable private video2: Video;
  @observable public active: boolean = false;
  @observable public reverseIntro: boolean = false;

  private overlay: any;

  public componentDidMount() {
    this.video1.play();
    this.video1.vid.volume = 0;

    IPMIFramework.Tracking.PersonEnteredSignal.add((blob: any) => {
      console.log("enter");
      this.startInteraction();
    });

    IPMIFramework.Tracking.PersonUpdatedSignal.add((blob: any) => {
      console.log("Moved");
    });

    IPMIFramework.Tracking.PersonLeftSignal.add((blob: any) => {
      console.log("leave");
      this.reverseIntro = true;
    });
  }

  public startInteraction() {
    /**/
    this.reverseIntro = false;
    this.video1.vid.volume = 1;
    this.active = true;
  }

  public stopInteraction() {
    /**/
    console.log("STOP");
    this.video1.vid.volume = 0;
    this.active = false;
  }

  public render() {
    console.log("Render", this.reverseIntro);

    return (
      <div>
        <Video
          loop={true}
          src="apps/sonnevanck/background.mp4"
          onReady={(video: any) => {
            this.video1 = video;
          }}
        />

        {this.active ? (
          this.reverseIntro ? (
            <Sequence
              key="a"
              baseUrl="apps/sonnevanck/logo_interaction/LOGO_MASK_TRANS-OUT_"
              fileExtention=".png"
              frameCount={74}
              width="100vw"
              height="100vh"
              reverse={true}
              onFinishedPlaying={() => this.stopInteraction()}
            />
          ) : (
            <Sequence
              key="b"
              baseUrl="apps/sonnevanck/logo_interaction/LOGO_MASK_TRANS-OUT_"
              fileExtention=".png"
              frameCount={74}
              width="100vw"
              height="100vh"
            />
          )
        ) : (
          <Sequence
            key="c"
            loop={true}
            baseUrl="apps/sonnevanck/logo/LOGO_MAIN_00-LOOP_"
            fileExtention=".png"
            frameCount={125}
            width="100vw"
            height="100vh"
          />
        )}
      </div>
    );
  }
}



