import * as React from "react";
import Video from "./components/video";
import DemoApp1 from "./apps/demoapp1";
import DemoApp2 from "./apps/demoapp2";
import Sonnevanck from "./apps/sonnevanck";
import Concordia from "./apps/concordia";

import { observable } from "mobx";
import { observer } from "mobx-react";

import { Redirect, Route, Switch } from "react-router-dom";

@observer
class App extends React.Component {
  @observable private finished = false;
  private apps = [
    { url: "/demo1", component: DemoApp1 },
    { url: "/demo2", component: DemoApp2 },
    { url: "/sonnevanck", component: Sonnevanck },
    { url: "/concordia", component: Concordia }
  ];

  public render() {
    // if (this.finished) {
    //   return <Redirect to={this.apps[Math.floor(Math.random() * this.apps.length)].url} />;
    // }
    //
    // if (window.location.pathname === "/") {
    //   window.location.href = "/concordia";
    // }

    return (
      <Concordia />
      // <div className="App">
      //   {this.apps.map(app => <Route key={app.url} exact path={app.url} component={app.component} />)}
      // </div>
    );
  }
}

export default App;



