import React = require("react");
import Video from "../components/video";
import Sequence from "../components/sequence";
import { ipmiConfig } from "../ipmi_config";
import { observable } from "mobx";
import { observer } from "mobx-react";

const IPMI = require("../lib/ipmi.js");
IPMI.Tools.DEBUG = true;
const IPMIFramework = new IPMI.Framework(ipmiConfig);

@observer
export default class Concordia extends React.Component {
  @observable private loop: Sequence;
  @observable private scrubber: Sequence;
  @observable public active: boolean = false;

  private overlay: any;

  public componentDidMount() {
    if (this.scrubber) {
      this.scrubber.pause();
    }
    IPMIFramework.Tracking.PersonEnteredSignal.add((blob: any) => {
      this.startInteraction();
    });

    IPMIFramework.Tracking.PersonUpdatedSignal.add((blob: any) => {
      /**/
      if (this.scrubber) {
        this.scrubber.currentFrame = Math.floor(this.scrubber.frameCount * blob.centroid.x);
      }
    });

    IPMIFramework.Tracking.PersonLeftSignal.add((blob: any) => {
      this.stopInteraction();
    });
  }

  public startInteraction() {
    /**/

    this.active = true;
  }

  public stopInteraction() {
    /**/
    this.active = false;
  }

  public render() {
    return (
      <div>
        <Sequence
          ref={(ref: Sequence) => (this.scrubber = ref)}
          key="1"
          baseUrl="apps/concordia/interactive/hackethon_InteractiveFrank"
          numsize="0000"
          fileExtention=".jpg"
          frameCount={136}
          width="100vw"
          height="100vh"
        />

        {this.active ? (
          ""
        ) : (
          <Sequence
            ref={(ref: Sequence) => (this.loop = ref)}
            key="2"
            loop={true}
            baseUrl="apps/concordia/loop/hackethon_AppleEatingFrank"
            numsize="0000"
            fileExtention=".jpg"
            frameCount={136}
            width="100vw"
            height="100vh"
            speed={0.5}
          />
        )}

        <h1
          style={{
            color: "#fff",
            fontSize: 90,
            position: "absolute",
            textAlign: "center",
            width: "100%",
            padding: "0px 0",
            bottom: -50,
            fontFamily: "arial"
          }}
        >
          Tekst
        </h1>
      </div>
    );
  }
}



